const Text = ({ type }) => {
    switch (type) {
        case "heading":
            return <h1>{children}</h1>;
        default:
            return (
                <React.Fragment>
                    <input type="text" {...props} />
                    <label htmlFor={name}>
                        {(
                            value.charAt(0).toUpperCase() +
                            value.slice(1) +
                            " "
                        ).replace("_", " ")}
                    </label>
                </React.Fragment>
            );
    }
};

export default Text;
